import back from '@/components/back'
import axios from 'axios'
import $load from '@/utils/loading'
export default {
    components: {
        back
    },
    data(){
        return{
            title: '患者档案',
            stringValue: false,
            tips: '',
            user: this.$store.state.userInfo,
            info: null,
            Patient: this.$store.state.patient,
            orderInfo: this.$store.state.pInfo,
            check: [],
            showBtn:false,
            doctorInfo:null,
            GZInfo:null,
            accessKey:"",
            isImEntry:this.$route.query.isImEntry,
            isShow:false
        }
    },
    created(){
        this.getInfo()
        //this.$store.dispatch('pushIsImEntry',1)
        this.$bridge.registerhandler('freshView', (data, responseCallback) => {
            this.getInfo()
            responseCallback('接收到用户数据')
        })
        setTimeout(()=>{
            if(this.isImEntry){
                this.isShow = false
            }
        },3000)
    },
    methods:{
    	goGZsums(){
    		//跳转到中山医的
            let _key = window.encodeURIComponent(this.accessKey)
            let url = this.GZInfo.Domain + "/index.html#/essential?accessKey=" + _key+ "&drid=" + this.user.Data.User.DoctorId
            if(this.$route.query.isImEntry){
                url = this.GZInfo.Domain + "/index.html#/essential?accessKey=" + _key+ "&drid=" + this.user.Data.User.DoctorId + "&isImEntry=1"
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                window.location.href = url
            } 
        },
        goManagement(){//跳转分组
            let url = `/patient/management/2?id=${this.info.PatientId}`
            if(this.$route.query.isImEntry){
                url = `/patient/management/2?id=${this.info.PatientId}&isImEntry=1`
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                this.$router.push(url)
            }  
        },  
        goNearly(){ //跳转病历资料
            let url =  `/nearly_info?patientId=${this.Patient.PatientId}`
            if(this.$route.query.isImEntry){
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                this.$router.push(url)
            } 
        }, 
        toService(){ //定制服务包
            let url = `/serviceP-list?patientId=${this.Patient.PatientId}`
            if(this.$route.query.isImEntry){
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                this.$router.push(url)
            } 
        },
        toTap(){ //疾病标签
            let url = '/dieaseList?fromArc=1'
            if(this.$route.query.isImEntry){
                url = '/dieaseList?fromArc=1&isImEntry=1'
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                this.$store.dispatch('pushSaveDiease',this.check)
                this.$router.push(url)
            } 
        },
		goReferral(){
            let url = '/teamList?Referral=1'
            if(this.$route.query.isImEntry){
                url = '/teamList?Referral=1&isImEntry=1'
                this.$bridge.callhandler('ToNewLink', url)
            }else{
                this.$router.push(url)
            } 
		},
        remark(){
            this.$get('PlatFormAPI/DoctorPatient/UpdatePatientDrRemark',{
                PatientId: this.Patient.PatientId,
                DrRemark: this.info.DrRemark,
            }).then(o => {
                this.$toast('备注修改成功')
                const obj = {
                    PatientId: this.Patient.PatientId,
                    DrRemark: this.info.DrRemark
                }
                this.$bridge.callhandler('changeRemark',JSON.stringify(obj))
            })
        },
        getInfo(){
        	axios.all([
				this.$get('PlatFormAPI/DoctorPatient/QueryDoctorPatientSummary',{
	                PatientId:this.Patient.PatientId,
	                DrId:this.user.Data.User.DoctorId,
	            }),
	            this.$get('PlatFormAPI/DiseaseGroupAndRule/QueryPatientDisGroup',{
	                PatientId:this.Patient.PatientId
	            }),
   	            this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId',{
   	                DrId: this.user.Data.User.DoctorId
   	            }),
			]).then(o => {
				if(!o[0].DrRemark){
                    o[0].DrRemark = ''
                }
                this.info = o[0]
                this.stringValue = o[0].IsDrKey == 0 ? false :true
                //保存患者信息
                let obj = {}
				obj = {
					"Birthday" : this.info.Birthday,
                    "PatientName" : this.info.PatientName,
                    "PatientId" : this.info.PatientId,
                    "AreaId" : null,
                    "AccountId" : this.info.AccountId,
                    "PatientSex" : this.info.PatientSex,
                    "AreaName" : null,
                    "Age" : this.info.Age,
				}
                this.$store.dispatch('pushpatient', obj)
                this.Patient = obj
                
                if(o[1]){
                    this.check = o[1]
                }
//                 if(o[2]){
//                 	this.showBtn = true	
//                 }
				this.$store.dispatch('pushmFItemNames', o[0].MFItemIds)
				
				//医生的详细信息
				this.doctorInfo = o[2]
				this.$store.dispatch('pushProLevel', o[2].ProfessionalLevel)
				$load.loadIn2()
				this.$post('PlatFormAPI/KnowledgeBase/GetGZSumsConfig',{
	                DoctorId: this.doctorInfo.DrId,
	                DepartId:this.doctorInfo.DepartmentId,
	                OrgId:this.doctorInfo.HospitalId,//医院id
	           }).then(a => {
	                this.GZInfo = a
	                if(this.GZInfo.HaveAuthority){
	                	this.$get('PlatFormAPI/KnowledgeBase/GetPatientEncrypt',{
			                PatientId: this.Patient.PatientId,
			            }).then(b => {
			                $load.loadOut2();
			                this.accessKey = b
			            }).catch(err => {
			            	$load.loadOut2();
			            })
	                }else{
	                	$load.loadOut2();
	                }
	            }).catch(err => {
	            	$load.loadOut2();
	            })
			})
        },
        change(c){
            const IsDrKey = c ? '1' : '0'
            this.$get('PlatFormAPI/DoctorPatient/SetDrKey',{
                DrPatientId: this.info.DrPatientId,
                IsDrKey
            }).then(o => {
                const obj = {
                    PatientId: this.Patient.PatientId,
                    isKey: IsDrKey
                }
                this.$bridge.callhandler('changeDrKey',JSON.stringify(obj))
            })
        },
        send(){
            this.$post('IMAPI/Message/GetNullIMRelationRecord',{
                DoctorId: this.user.Data.User.DoctorId,
                PatientId: this.Patient.PatientId
            }).then(o => {
                o.PatientInfo = {
					PatientId: this.Patient.PatientId,
					PatientName: this.Patient.PatientName,
					PatientSex: this.Patient.PatientSex,
					Birthday: this.Patient.Birthday,
					Age: this.Patient.Age,
				}
				o.DoctorPatientRelation = {
					DrPatientId: this.info.DrPatientId,
                }
                this.$bridge.callhandler('SendMessage', JSON.stringify(o))
            })
        },
        
    }
}



